.boxDiv::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  bottom: 0;
  right: 0;
  z-index: -1;
  opacity: 0.8;
  margin: -3.1cm 0cm 0cm -3.5cm;

