@keyframes moveHi {
  0% {
    margin: -1.2cm 0cm 0cm -3.5cm;
  }

  20% {
    margin: -1.7cm 0cm 0cm -3.5cm;
  }
  40% {
    margin: -2.2cm 0cm 0cm -3.5cm;
  }
  60% {
    margin: -2.7cm 0cm 0cm -3.5cm;
  }
  100% {
    margin: -3.1cm 0cm 0cm -3.5cm;
  }
}

@keyframes shakeHi {
  0% {
    transform: rotate(0deg);
  }
  7% {
    transform: rotate(-3deg);
  }

  20% {
    transform: rotate(-6deg);
  }
  40% {
    transform: rotate(-9deg);
  }
  60% {
    transform: rotate(-6deg);
  }
  80% {
    transform: rotate(-3deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

